body {
  background: #9a0922;
  color: white;
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE/Edge */
  user-select: none;
}
.homePage {
  position: relative;
}
.unicorn-studio-embed {
  width: 100%;
  height: 120vh;
  padding: 0;
  margin: 0;
  color: white;
}
.unicorn-studio-embed a {
  opacity: 0;
}
.hideWaterMark {
  width: 100%;
  height: 100vh;
  display: flex;
  place-items: center;
  overflow: hidden;
}
.placeholder {
  background: url("./images/placeholder.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  height: 100vh;
  margin: 0;
}
