.navbar {
  box-sizing: border-box;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 20px;
  position: absolute;
}
.navbar .title {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 25px;
  color: white;
  font-family: sans-serif;
  text-shadow: 0px 0px 20px #ffffff00;
  text-decoration: underline;
  text-underline-offset: 7px;
  transition: all 0.1s ease-in;
  animation: underlines 2s ease-in alternate;
}
.navbar .title:hover,
.navlinks a:hover {
  color: #ffffff;
  text-decoration: underline;
  text-underline-offset: 2px;
  text-shadow: 0px 0px 20px #ffffff;
  cursor: pointer;
}
@keyframes underlines {
  0% {
    transform: translateY(40px);
    text-underline-offset: 40px;
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  80% {
    transform: translateY(40px);
    text-underline-offset: 40px;
  }
  100% {
    transform: translateY(0px);
    text-underline-offset: 2px;
  }
}
.navlinks {
  display: flex;
  justify-content: center;
  place-items: center;
  gap: 25px;
  font-size: 25px;
  padding: 10px 20px;
  backdrop-filter: blur(10px);
  border-radius: 10px;
}
.navlinks a {
  text-shadow: 0px 0px 30px #ff0000;
  transition: all 0.1s ease-in;
  text-decoration: none;
  text-underline-offset: 7px;
}
